<template>
    <div>
        
        <template>
  <v-card
    :loading="loading"
    class="mx-auto my-12 noticias__card"
    max-width="250"
  >
    <template slot="progress">
      <v-progress-linear
        color="deep-purple"
        height="10"
        indeterminate
      ></v-progress-linear>
    </template>

    <v-img
      height="200"
      :src="require(`@/assets/img/noticias/${value.Imagen}`)"
    ></v-img>

    <v-card-title>{{value.Titulo}}</v-card-title>

    <v-card-text style="height: 110px">
      <div>{{(value.Texto.length > 120) ? value.Texto.substring(0, 120)+'...' : value.Texto}}</div>
    </v-card-text>

    <v-divider class="mx-4"></v-divider>

    <v-card-actions>
      <v-btn
        color="verde"
        text
        @click="reserve"
      >
        <span class="text-capitalize">Leer más</span>
      </v-btn>
    </v-card-actions>
  </v-card>
</template>


    </div>
</template>


<script>
export default {
    name: 'CardNoticias',

    props:{
        value:{
            Titulo:{
                type: Text,
                default: ''
            },
            Texto:{
                type: Text,
                default: ''
            },
            Imagen:{
                type: Text,
                default: ''
            }
        }
    },

     data: () => ({
      loading: false,
      selection: 1,
    }),

    methods: {
      reserve () {
        this.loading = true

        setTimeout(() => (this.loading = false), 2000)
      },
    }
}
</script>


<style>
.noticias__card {
    margin: 10px !important;
}
</style>
